    'baseBio':'Militar y político. Conocido como el Libertador de Argentína, Chile y Perú.',
    'bio': 'José Francisco de San Martín y Matorras (Yapeyú, Virreinato del Río de la Plata; 25 de febrero de 1778-Boulogne-sur-Mer, 17 de agosto de 1850) fue un militar y político argentino y uno de los libertadores de Argentina, Chile y Perú. Es una de los dos figuras mas trascendentes de la Guerra de Independecia Americana junto a Simón Bolívar.',
    'bio2': '​ En abril de 1784, cuando tenía seis años, llegó con su familia a la ciudad española de Cádiz ―previa estadía en Buenos Aires― y se radicó luego en la ciudad de Málaga. Comenzó sus estudios en el Real Seminario de Nobles de Madrid y en la Escuela de Temporalidades de Málaga en 1786. Ingresó posteriormente al ejército español e hizo su carrera militar en el Regimiento de Murcia. ​Combatió en el norte de África, luego contra la dominación napoleónica de España y participó en las batallas de Bailén y La Albuera. Con 34 años, en 1812, tras haber alcanzado el grado de teniente coronel, y luego de una escala en Londres, retornó a Buenos Aires, donde se puso al servicio de la independencia de las Provincias Unidas del Río de la Plata.',
    'bio3': '​Se le encomendó la creación del Regimiento de Granaderos a Caballo (que hoy lleva su nombre), que tuvo su bautismo de fuego en el combate de San Lorenzo. ​Más tarde se le encargó la jefatura del Ejército del Norte, en reemplazo del general Manuel Belgrano.​ Allí concibió su plan continental, comprendiendo que el triunfo patriota en la guerra de la independencia hispanoamericana solo se lograría con la eliminación de todos los núcleos realistas que eran los centros de poder leales a mantener el sistema colonial en América. Nombrado gobernador de Cuyo, con sede en la ciudad de Mendoza, puso en marcha su proyecto:​ tras organizar al Ejército de los Andes, cruzó la cordillera del mismo nombre y lideró la emancipación de Chile, en las batallas de Chacabuco y Maipú. Luego, utilizando una flota organizada y financiada por Chile, y luego de recibir instrucciones del Senado de Chile, atacó al centro del poder español en Sudamérica, la ciudad de Lima, y declaró la independencia del Perú en 1821.',
    'bio4': 'Finalizó su carrera de las armas luego de producida la Entrevista de Guayaquil con Simón Bolívar, en 1822, donde le cedió su ejército y la meta de finalizar la liberación del Perú. Partió hacia Europa, donde murió el 17 de agosto de 1850. En Argentina se lo reconoce como el Padre de la Patria​ y el «Libertador»,​ y se lo valora como el principal héroe y prócer del panteón nacional. En Perú también se lo recuerda como el «Fundador de la Libertad del Perú», el «Fundador de la República», el «Generalísimo de las Armas» y el «Protector» del país. ​El Ejército de Chile le reconoce el grado de capitán general.'
