    'bio': 'Manuel José Joaquín del Corazón de Jesús Belgrano (Buenos Aires, Virreinato del Perú, Imperio español, 3 de junio de 1770-ibídem, Provincias Unidas del Río de la Plata, 20 de junio de 1820) fue un abogado, economista, periodista, político, diplomático y militar rioplatense de destacada actuación en la actual Argentina, el Paraguay y el Alto Perú durante las dos primeras décadas del siglo xix.',
    'bio1': 'Participó en la defensa de Buenos Aires, capital del Virreinato del Río de la Plata, en las dos Invasiones Inglesas —1806 y 1807— y promovió la emancipación de Hispanoamérica respecto de España en apoyo a las aspiraciones de la princesa Carlota Joaquina en la región, aunque sin éxito. Fue uno de los principales patriotas que impulsaron la Revolución de Mayo, por la cual se destituyó al virrey Baltasar Hidalgo de Cisneros, y fue vocal de la Primera Junta de gobierno que lo reemplazó.',
